/* ============================================================
   LAYOUT GLOBAL — Starter Kit
   ------------------------------------------------------------
   Usa apenas variáveis CSS (var(--bg), var(--text-primary), etc.)
   Compatível com todos os temas e com o theme.js
============================================================ */
/* ============================================================
   LAYOUT GLOBAL — Starter Kit
============================================================ */

/* Evita flicker de tema */
html {
  visibility: hidden;
}

html.light-theme,
html.dark-theme,
html.hydra-theme,
html.dracula-theme,
html.neon-theme,
html.solarized-theme {
  visibility: visible;
}

/* Agora sim, o layout */
body {
  background-color: var(--bg);
  color: var(--text-primary);
  background-image: var(--bg-img);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: var(--font-family, "Inter", sans-serif);
}

/* LINKS */
a {
  color: var(--link);
  text-decoration: none;
  transition: color 0.2s ease;
}

a:hover {
  color: var(--link-hover);
}

/* MAIN WRAPPER */
.main {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  /* Fim da 1ª parte de tutorial que serve como base para o layout global, 'https://youtu.be/_a5j7KoflTs?t=1946' */
  width: min(90%, 1200px);
  margin: 0 auto;
  gap: 2rem;
  padding: 2rem 0;
}

/* PARÁGRAFOS — estilo base */
.main_paragraph1,
.main_paragraph2 {
  font-weight: 600;
  line-height: 1.6;
  transition: color 0.2s ease;
}


/* RESPONSIVE */
@media (max-width: 768px) {
  .main {
    flex-direction: column;
    gap: 1.5rem;
  }
}

@media (max-width: 480px) {
  .main {
    width: 95%;
    gap: 1rem;
  }

  .main_paragraph1,
  .main_paragraph2 {
    font-size: 0.9rem;
    text-align: center;
  }
}
.main_paragraph1:hover {
  color: var(--accent);
  filter: brightness(1.25);
}

.main_paragraph2:hover {
  color: var(--link-hover);
  filter: brightness(1.15);
}

