.site-footer {
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--text-primary);
}

.social-media {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding: 1rem 0;

  a {
    color: var(--link);
    transition: color 0.3s ease;

    &:hover {
      color: var(--accent);
    }
  }
}
